import React, { Fragment } from 'react';
import {
  H1,
  H2,
  Content,
  InfoText,
} from 'components/styles';
import { NavLinks, NextLink } from 'components/Links';

const WtfIsJs = () => (
  <Fragment>
    <H1>1. <span>WTF is JS</span></H1>
    <Content>
      <InfoText>
        Este  o captulo inicial do minicurso de JavaScript, abordei a seguir algumas informaes e curiosidades sobre
        a linguagem.
      </InfoText>
      <H2>Origem</H2>
      <H2>Evoluo</H2>
      <H2>Aplicaes</H2>
      <H2>Vanilla X Frameworks</H2>
      <H2>Node? React? Redux?</H2>
      <NavLinks>
        <NextLink to="/2-basics">2. Bsico</NextLink>
      </NavLinks>
    </Content>
  </Fragment>
);

export default WtfIsJs;



